import { Component } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { RotasEnum } from "./shared/models/enums/RotasEnum";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"],
})
export class AppComponent {
  public page: string;
  public title = "Teacher Flavia";
  public linkWhatsapp: string =
    "https://api.whatsapp.com/send?phone=5561996825543&text=Oi%2C%20Flavia.%20Eu%20quero%20saber%20como%20melhorar%20meu%20ingl%C3%AAs!";

  public pageHome = RotasEnum.HOME;
  public pageQuemSou = RotasEnum.QUEM_SOMOS;
  public pagePlanos = RotasEnum.PLANOS;

  constructor(private router: Router, private route: ActivatedRoute) {}

  ngOnInit() {
    this.route.queryParams.subscribe((param) => {
      if (param.page != "" && param.page != undefined && param.page != null) {
        this.page = param.page;
      } else {
        this.page = RotasEnum.HOME;
        this.router.navigate([RotasEnum.NONE], {
          queryParams: { page: RotasEnum.HOME },
        });
      }
    });
    // this.router.config.forEach((config) => {
    //   if (config.path && location.href.includes(config.path)) {
    //     this.router.navigate([config.path]);
    //     return;
    //   }
    // });
  }

  public abrirWhatsapp() {
    window.open(this.linkWhatsapp, "_blank");
  }
}
