import { Component, Input, OnInit } from "@angular/core";

@Component({
  selector: "app-video",
  templateUrl: "./video.component.html",
  styleUrls: ["./video.component.scss"],
})
export class VideoComponent implements OnInit {
  public myVideo: any = document.getElementById("video");

  @Input("src")
  public src: string;

  @Input("width")
  public width: string;

  constructor() {}

  ngOnInit() {}

  public playPause() {
    if (this.myVideo.paused) {
      this.myVideo.play();
    } else {
      this.myVideo.pause();
    }
  }
}
