import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { RotasEnum } from "src/app/shared/models/enums/RotasEnum";

@Component({
  selector: "app-navbar",
  templateUrl: "./navbar.component.html",
  styleUrls: ["./navbar.component.scss"],
})
export class NavbarComponent implements OnInit {
  public pages: { nome: string; rota: string; classe: string }[] = [];

  constructor(private router: Router) {}

  ngOnInit() {
    this.criarAbas();

    this.pages.forEach((page) => {
      if (location.href.includes(page.rota)) {
        page.classe = "nav-link active";
      }
    });
  }

  selecionarAba(page: { nome: string; rota: string; classe: string }) {
    this.pages.forEach((pag) => {
      if (page.nome == pag.nome) {
        pag.classe = "nav-link active";
        this.router.navigate([RotasEnum.NONE], {
          queryParams: { page: pag.rota },
        });
      } else {
        pag.classe = "nav-link ";
      }
    });
  }

  criarAbas() {
    this.pages.push({
      nome: "Home",
      rota: RotasEnum.HOME,
      classe: "nav-link ",
    });
    this.pages.push({
      nome: "Planos",
      rota: RotasEnum.PLANOS,
      classe: "nav-link ",
    });
    this.pages.push({
      nome: "Quem Sou",
      rota: RotasEnum.QUEM_SOMOS,
      classe: "nav-link ",
    });
  }
}
